import {Component, OnInit, ViewChild} from '@angular/core';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanConfigProvider} from '../../../../providers/africlean-config/africlean-config.provider';


@Component({
    selector: 'app-client-management',
    templateUrl: './client-management.page.html',
    styleUrls: ['./client-management.page.scss'],
})
export class ClientManagementPage implements OnInit {

    /**
     * Liste des clients
     */
    public clients: any[];
    /**
     * numéro des pages
     */
    public positions = [];

    /**
     * Position de la page courante
     */
    public currentPage = 1;

    /**
     * Annonces temporaires
     */
    public clientsTmp: any[];

    /**
     * La limite pour récuperer les données
     */
    public limit = 14;

    /**
     * Données à ignorer
     */
    public skip = 0;

    /**
     * Le nombre d'annonces par pages
     */
    public nbrClientsPerPage = 7;
    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    @ViewChild('content', {static: true}) public content: any;

    public user: any;

    public customerType = 'particulier';

    constructor(private userProvider: AfricleanHttpUserProvider,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private config: AfricleanConfigProvider) {
    }

    ngOnInit() {
        // initialisation tableau positions
        this.positions = [];
        this.getClientsByFilter();
        // Test la platforme
        this.isWeb = this.config.isWeb();
        this.observableProvider.loadClient.subscribe(
            (load) => {
                if (load) {
                    this.limit = 14;
                    this.skip = 0;
                    this.clients = [];
                    this.clientsTmp = [];
                    this.positions = [];
                    this.getClientsByFilter();
                }
            });

    }
    segmentChanged(ev: any) {
        this.limit = 14;
        this.skip = 0;
        this.clients = [];
        this.clientsTmp = [];
        this.positions = [];
        this.customerType = ev.detail.value;
        this.getClientsByFilter();
    }
    /**
     * Recupere la position de la page courante et permet de savoir si c'est un suivant
     * donc il faut requeter de nouveau (multiple de 5) ou si c'est un precedent il ne faut
     * rien faire dans ce cas
     * @param position Position de la page
     */
    sendPosition(position) {
        this.currentPage = position;
        if (position % 2 === 0 && !this.positions.includes(position)) {
            this.positions.push(position);
            this.skip = this.skip + this.limit;
            this.getClientsByFilter();
        }
    }

    getCustomerType() {
        switch (this.customerType) {
            case 'particulier':
               return  1;
            case 'pro':
                return  2;
            default:
                return 3;
        }
    }

    private async getClientsByFilter() {
        this.user = await this.session.getItem('user');
        const entity =  (this.user) ? this.user.entity : null;
        const id = (entity) ? entity.id : null;
        let body: any = {};
        const customerType = this.getCustomerType();
        body = {
            limit: this.limit,
            skip: this.skip,
            customerType,
            entity: id
        };
        console.log(body);
        this.userProvider.save('client/clients', body).subscribe(
            (clients) => {
                if (!this.clients && !this.clientsTmp) {
                    this.clients = [];
                    this.clientsTmp = [];
                    this.positions = [];
                }
                this.clients = this.clients.concat(clients.slice());
                this.clientsTmp = this.clientsTmp.concat(clients.slice());
            }
        );
    }
}
